@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline; }

body {
  font-family: "Raleway", sans-serif;
  color: #333;
  background-color: #333;
  font-weight: 400;
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  height: 100vh;
  transition: all 0.3s ease-in-out; }

div {
  position: relative;
  z-index: 2;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out; }

.container {
  background-color: #fff;
  width: 450px;
  border-radius: 3px;
  padding: 40px 50px; }

.quote-text {
  text-align: center;
  transition: all 0.3s ease-in-out; }
  .quote-text #left-quote {
    font-size: 1r em;
    margin-right: 0.4rem; }
  .quote-text #right-quote {
    font-size: 1rem;
    margin-left: 0.4rem; }

div {
  position: relative;
  z-index: 2; }

.footer {
  width: 450px;
  text-align: center;
  display: block;
  margin: 15px auto 30px auto;
  font-size: 0.8em;
  color: #fff; }
  .footer a {
    font-weight: 500;
    text-decoration: none;
    color: #fff; }

#quote-box {
  border-radius: 3px;
  position: relative;
  width: 450px;
  padding: 40px 50px;
  display: table;
  background-color: #fff; }
  #quote-box .quote-text {
    text-align: center;
    width: 450px;
    height: auto;
    clear: both;
    font-weight: 500;
    font-size: 1.75em; }
    #quote-box .quote-text i {
      font-size: 1em;
      margin-right: 0.4em; }
  #quote-box .quote-author {
    width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 1em;
    text-align: right; }
  #quote-box .buttons {
    width: 450px;
    margin: auto;
    display: block; }
    #quote-box .buttons .button {
      height: 38px;
      border: none;
      border-radius: 3px;
      color: #fff;
      background-color: #333;
      outline: none;
      font-size: 0.85em;
      padding: 8px 18px 6px 18px;
      margin-top: 30px;
      opacity: 1;
      cursor: pointer; }
      #quote-box .buttons .button:hover {
        opacity: 0.9; }
      #quote-box .buttons .button#tweet-quote, #quote-box .buttons .button#tumblr-quote {
        float: left;
        padding: 0px;
        padding-top: 8px;
        text-align: center;
        font-size: 1.2em;
        margin-right: 5px;
        height: 30px;
        width: 40px;
        transition: all 0.3s ease-in-out; }
      #quote-box .buttons .button#new-quote {
        float: right; }

